/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .mkdf-widget-title-holder {
        margin-bottom: $widget-list-item-padding;

        .mkdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
        }

        .mkdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }
    &.widget_categories{
        ul{
            li{
                a{
                    @include mkdfH6();
                    line-height: 26px;
                    text-transform: uppercase;
                }
            }
        }
    }
    &.mkdf-blog-list{
        line-height: 25px;
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            border-bottom-color: transparent;
            border-bottom-style: $widget-list-item-border-style;
            border-bottom-width: $widget-list-item-border-width;
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: 2px 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $default-text-color;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            padding-left: 12px;
        }
    }
    .select2-container--default .select2-selection--single .select2-selection__rendered{
        color: $footer-text-color;
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu,
    &.widget_product_categories {
        .mkdf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;
                    transition: padding .2s ease;

                    &:after {
                        content: "\4c";
                        display: inline-block;
                        font-family: "ElegantIcons";
                        font-size: 13px;
                        left: -15px;
                        line-height: 12px;
                        position: absolute;
                        text-transform: none;
                        top: 50%;
                        vertical-align: middle;
                        speak: none;
                        text-rendering: auto;

                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;

                        transform: translateY(-50%);
                        transition: left .2s cubic-bezier(0.54, 0.49, 0, 1.75);
                    }

                    &:hover {
                        padding-left: 15px;
	                    padding-right: 0;

                        &:after {
                            left: 0;
                        }
                    }
                }
            }
        }
    }

	&.widget_archive,
	&.widget_categories {
		ul li a {
			padding-right: 0;
		}
	}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .mkdf-widget-title {
            margin-bottom: 0;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;

        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: #fff;
            color: $first-main-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }


    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .mkdf-searchform {
        &.mkdf-searchform-block {
            display: flex;
            width: 100%;
    
            .input-holder {
                width: 100%;
            }
        }
        
        .input-holder {
            border-color: $default-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            position: relative;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                border-radius: 2px;
                float: left;
                font-family: $default-text-font;
                font-size: 16px;
                font-weight: inherit;
                height: $widget-search-form-height - 2px;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                transition: color .2s ease-in-out;
            }
            input{
                line-height: 51px;
            }
            button{
                line-height: 50px;
                position: absolute;
                right: -1px;
                top: -1px;
                height: calc(100% + 2px);

                svg{
                    display: block;
                    margin: 0 auto;

                    path{
                        fill: $default-heading-color;
                    }
                }

                &:hover{
                    svg{
                        path{
                            fill: #fff;
                        }
                    }
                }
            }

            input.search-field {
                padding: 0 5px 0 21px;
                width: calc(100% - #{$widget-search-form-height});

                @include mkdfPlaceholder {
                    @include mkdfH6();
                    font-size: 16px;
                    font-style: normal;
                    letter-spacing: 0;
                    text-transform: capitalize;
                    font-weight: 500;
                    color: $footer-text-color;
                    line-height: 47px;
                }
            }

            button {
                background: #f2f3f5;
                color: #001659;
                cursor: pointer;
                font-size: 14px;
                width: $widget-search-form-height;
                transition: color .2s ease-in-out;

                &:hover {
                    color: #fff;
                    background-color: $first-main-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;
            transition: color .2s ease-in-out;
        }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {

        margin: 16px 0 0;

        a {
            @include mkdfH6();
            font-size: 14px!important;
            padding: 11px 24px;
            line-height: 3.9em;
            background-color: #f2f3f5;
            margin-right: 10px;
            color: #2b3651;
            border-radius: 2px;
            transition: background .1s ease-in-out;
            white-space: nowrap;
            text-transform: uppercase;

            @include phone-landscape{
                display: inline-block;
                padding: 0 24px;
                margin: 7px 0;
                line-height: 3.7em;
            }

            &:hover {
                background-color: $first-main-color;
                color: #fff;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}