.mkdf-top-bar {
    .widget {
        color: $default-heading-color;
        font-weight: 400;

        a {
            color: $default-heading-color;

            &:hover {
                color: $first-main-color;
            }
        }
    }

    &.mkdf-top-bar-light-skin{
        .widget {
            color: #fff;

            a {
                color: #fff;

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}