footer {
    // widgets spacing
    $widget-bottom-margin: 24px;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 7px;

    // list items color
    $widget-list-item-background-color: #999999;

    // list items border
    $widget-list-item-border-color: #cccccc;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: #cfd3e5;
    $widget-heading-color: #ffffff;

    // links
    $widget-link-color: #cfd3e5;
    $widget-link-hover-color: $first-main-color;

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .mkdf-footer-top-holder {
        .widget,
        .widget p,
        .widget a{
            color: #9ca5af;
            font-size: 18px;
        }

        .widget a{
            &:hover{
                color: $first-main-color;
            }
        }
    }

    .mkdf-footer-bottom-holder{
        .widget,
        .widget p,
        .widget a{
            color: #9ca5af;
            font-size: 14px;
            margin-bottom: 0;
        }

        .widget a{
            &:hover{
                color: $first-main-color;
            }
        }
    }
    .mkdf-social-icons-group-widget{
        &.mkdf-square-icons{
            .mkdf-social-icon-widget-holder{

                border-top: none;

                span{
                    color: inherit;
                }

                &:hover{
                    border-color: $first-main-color;
                    background-color: $first-main-color;
                    border-right: unset;
                    border-top: unset;
                }
                &:last-child{

                    &:hover {
                        border-right: 1px solid #1d263f;
                    }
                }
            }
        }
    }
    .mkdf-icon-widget-holder{
        display: flex;
        margin: 0 0 14px;

        .mkdf-icon-text{
            padding: 0 0 0 19px;
            font-size: 18px;

            @include ipad-landscape{
                padding: 0 60px 0 15px;
            }
        }

        .mkdf-icon-element{
            position: relative;
            top: 3px;
        }
    }
    .textwidget {
        .mkdf-footer-copy-text {
            @include ipad-landscape {
                content: initial;
                margin-left: 0 !important;
                margin-top: 0;
                padding-top: 7px;
            }
        }
    }

    .mkdf-btn{
        &.mkdf-footer-logo-btn{
            padding: 15px 111px;
            border-radius: 0;

            &:hover{
                box-shadow: none !important;
                color: #fff;
            }
        }
    }
}