@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 57px 0 20px;
    }

    .mkdf-comment {

        &.mkdf-pingback-comment {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            >div{
                &:not(.mkdf-pingback-comment){
                    margin: 33px 0 0;
                    padding: 35px 0 0;
                }
            }
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;

            >.mkdf-comment{
                margin: 0;
                padding: 0;
            }
        }



        .children {
            padding: 0 0 0 81px;
        }
    }

    .mkdf-comments-title {
        margin-bottom: 19px;

        > * {
            margin: 0;
            font-size: 40px;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 112px;
        height: 112px;
        display: block;
        float: left;

        img {
            display: block;
            border-radius: none;
        }
    }

    .mkdf-comment-text {
        padding: 0 0 0 142px;
        min-height: 82px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        .replay,
        .comment-edit-link {
            float: left;
            margin-left: 10px;
            font-size: 12px;
            color: #000;

            &:hover {
                color: $first-main-color;
            }
        }

        .comment-edit-link,
        .comment-reply-link{
            font-size: 16px;
            text-transform: capitalize;
            letter-spacing: 0.02em;
            float: left;
            font-weight: 500;
            font-family: $default-text-font;
            transition: color .1s ease-in-out;
            &:after{
                content: "\4c";
                font-family: ElegantIcons;
                font-size: 20px;
                margin-left: 15px;
                display: inline-block;
                padding: 12px 15px;
                background-color: #fff;
                vertical-align: middle;
                border-radius: 2px;
                margin-bottom: 5px;
            }
            &:hover{

                color: $default-heading-color;

                &:after{
                    background-color: $first-main-color;
                    color: #fff;
                    box-shadow: $first-hover-box-shadow;
                    transition: box-shadow .1s ease-in-out;
                }
            }
        }

        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            float: left;
            margin: 0;
            font-size: 12px;
        }

        .mkdf-text-holder p {
            margin: 5px 0 12px;
            font-size: 18px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .mkdf-comment-form-inner {
        padding: 75px 0 0;

        @include ipad-landscape{
            padding: 79px 0 0;
        }
    }

    .comment-reply-title {
        margin: 0;
        @include mkdfH3();
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
    .comment-form-cookies-consent{
        margin: 5px 0 30px;

        #wp-comment-cookies-consent{
            margin: 3px 18px 3px 4px;
        }

    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

