/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */

.mkdf-ms-explorer{
    .mkdf-owl-slider {
        .owl-nav{
            .owl-prev,
            .owl-next{
                top: 90.5%;
            }
        }
    }
}

.mkdf-owl-slider {
    visibility: hidden;

    &.mkdf-ig-slider{
        .owl-nav{
            .owl-prev {
                left: 0;

                &:hover{
                    & + .owl-next{
                        background-color: #fff;
                        color: #000;

                        @include phone-landscape{
                            background-color: #fff;
                        }
                    }
                }

                @include phone-landscape{
                    left: 0;
                }
            }
            .owl-next{
                @include phone-landscape{
                    color: #fff;
                }
            }
        }
    }

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            width: 78px;
            height: 78px;
            line-height: 78px;
            padding: 0;
            margin: 0;
            color: $default-heading-color;
            background: #fff;
            border: 0;
            border-radius: $default-border-radius;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            transform: translateY(-50%);
            transition: all .2s ease-out;

            @include phone-landscape{
                background: transparent;
            }

            &:hover {
                background-color: $first-main-color;
                @include phone-landscape{
                    background-color: transparent;
                }

                color: #fff;
            }

            .mkdf-prev-label,
            .mkdf-next-label,
            .mkdf-alt-prev-icon,
            .mkdf-alt-next-icon{
                display: none; // to hide it default from all slider but will be shown for certain
            }

            > span {
                position: relative;
                display: block;
                vertical-align: middle;
                line-height: 1;
                font-size: 34px;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 0;

            &:hover{
                & + .owl-next{
                    background-color: #fff;
                    color: #000;

                    @include phone-landscape{
                        background-color: transparent;
                    }
                }
            }

            @include phone-landscape{
                left: 0;
            }
        }

        .owl-next {
            right: 0;
            background-color: $first-main-color;
            color: #fff;

            @include phone-landscape{
                right: 0;
                background-color: transparent;
                color: #000;

                &:hover{
                    color: #fff;
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            &:nth-child(3n+1){
                span {
                    background-color: $second-main-color;
                    border-color: $second-main-color;
                }
            }

            &:nth-child(3n+2){
                span {
                    background-color: $second-main-color;
                    border-color: $second-main-color;
                }
            }

            &:nth-child(3n){
                span {
                    background-color: $second-main-color;
                    border-color: $second-main-color;
                }
            }

            &.active,
            &:hover {
                span{
                    transform: scale(1.2);
                }
            }

            span {
                display: inline-block;
                vertical-align: middle;
                width: 7px;
                height: 7px;
                border-radius: 50%;
                background-color: transparent;
                border: none;
                box-sizing: border-box;
                transform-origin: center center;
                transition: all 0.2s ease-out;
                will-change: transform;
            }

            //&:hover,
            //&.active {
            //
            //    span {
            //        background-color: initial;
            //        border-color: initial;
            //    }
            //}
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            //&.active {
            //    img {
            //        border: initial;
            //    }
            //}
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

