   #404 styles - start
   ========================================================================== */

.error404 {
    .mkdf-wrapper {
        padding: 0 !important;
    }

    .mkdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .mkdf-content {
            height: calc(100vh - 32px);
        }
    }

    .mkdf-content-inner {
        position: relative;
        height: 100%;

        @include ipad-landscape{
            height: 100vh;
        }
    }

    .mkdf-container-inner{
        height: 100%;
    }

    .mkdf-404-table-holder{
        @include mkdfTableLayout();
    }

    .mkdf-404-table-cell-holder{
        @include mkdfTableCellLayout(middle);
        padding-top: 5%;

        @include laptop-landscape-large{
            padding-top: 15%;
        }

        @include laptop-landscape-mac{
            padding-top: 22%;
        }
    }

    .mkdf-page-not-found {
        @include mkdfTableCellLayout();
        text-align: left;

        .mkdf-404-intro-title{
            position: absolute;
            left: -13.5%;
            top: -94px;
            font-size: 180px;
            line-height: 1;
            text-transform: uppercase;
            white-space: nowrap;
            font-weight: 600;
            letter-spacing: -0.01em;
            -webkit-text-stroke-color: #d2d2d4;
            -webkit-text-stroke-width: 1px;
            -webkit-text-fill-color: transparent;
            z-index: -1;

            @include laptop-landscape-large{
                left: -60px;
            }
            @include ipad-landscape{
                top: -83px;
                font-size: 154px;
            }
            @include ipad-portrait{
                top: -15px;
            }
            @include phone-landscape{
                left: -15px;
                font-size: 70px;
                top: 42px;
            }
        }

        .mkdf-404-title {
            margin: 0;
        }

        .mkdf-404-subtitle {
            margin: 35px 0 0;
        }

        .mkdf-404-text {
            margin: 14px 0 10px;
        }

        .mkdf-btn {
            margin: 22px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .mkdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .mkdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
