/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    display: table-cell;
    text-align: center;
    width: 39px;
    right: 25px;
    bottom: 25px;
    border-radius: 2px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    background-color: $first-main-color;
    border: 1px solid $first-main-color;
    padding: 11.5px 5px;
    transition: .3s;

    &:hover {
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        box-shadow: $first-hover-box-shadow;

        rect:nth-of-type(1) {
            transform: scaleY(1);
        }

        polygon {
            transform: translateY(0);
        }
    }

    &.mkdf-dark {
        background-color: $fourth-blue-color;
        border: 1px solid $fourth-blue-color;

        &:hover {
            background-color: $fourth-blue-color;
            border: 1px solid $fourth-blue-color;
            box-shadow: 0 10px 20px 0 rgba(0,16,42,.4);
        }
    }

    svg {
        vertical-align: middle;
        overflow: visible;

        g {
            stroke: #fff;
        }

        rect:nth-of-type(1) {
            transform: scaleY(0);
            transition: .2s;
            transform-origin: bottom;
        }

        polygon {
            transform: translateY(4px);
            transition: .2s;
        }
    }

    

    &.off {
        opacity: 0;
        visibility: hidden;
    }

    &.on {
        opacity: 1;
        visibility: visible;
    }

    > span {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 41px;
        font-size: 15px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        overflow: hidden;
        transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out, box-shadow .5s cubic-bezier(.49,.79,.17,.94);

        i {
            transition: .15s ease .05s;
        }

        &:after {
            content: '';
            position: absolute;
            z-index: 1;
            top: 12px;
            left: 19px;
            height: 0;
            width: 0;
            opacity: 0;
            border: 1px solid $default-border-color;
            transition: 0.2s cubic-bezier(0.54, 0.13, 0, 1.43);
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
