/* ROW BACKGROUND*/

.vc_row {
    &.mkdf-row-has-background-text {
        position: relative;
    }
}

.mkdf-elementor-row {
    width: 100%;

    &.mkdf-row-has-background-text {
        position: relative;
        margin: 0 auto;

    }
}

.mkdf-row-background-text-holder {
    @include mkdfAbsoluteHolderLayout();
    z-index: 0;
    white-space: nowrap;

    &.mkdf-row-background-text-front{
        z-index: 0;
    }
    &.mkdf-row-background-text-back{
        z-index: -1;
    }

    .mkdf-row-background-text-wrapper{
        @include mkdfTableLayout();

        .mkdf-row-background-text-wrapper-inner{
            display: table-cell;
            vertical-align: middle;
            font-size: 180px;
            color: #f5f6f7;
            -webkit-text-fill-color: transparent; /* Will override color (regardless of order) */
            -webkit-text-stroke-width: 1px;
            -webkit-text-stroke-color: #d2d2d4;

            .mkdf-row-background-text-1,
            .mkdf-row-background-text-2{
                text-transform: uppercase;
                font-weight: 700;
                line-height: 1;
            }
        }

        @include ipad-landscape{
            display: none;
        }

        &.mkdf-row-background-text-animation {
            visibility: hidden;

            .no-touchevents & {
                .mkdf-char-mask {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;

                    > span {
                        display: inline-block;
                        transform: translate3d(-100%,0,0);
                        transition: transform .5s ease;
                    }
                }

                &.mkdf-row-background-text-align-right {
                    .mkdf-char-mask > span {
                        transform: translate3d(100%,0,0);
                    }
                }

                .mkdf-char-mask > span.mkdf-show {
                    transform: translate3d(0,0,0);
                }
            }
        }
    }
}

.vc_row{
    &.mkdf-row-with-bg-pattern{
        position: relative;

        .mkdf-row-grid-section &{
            position: static;
        }
    }
}

.mkdf-elementor-row.mkdf-elementor-section-with-custom-bg-color section{
    background-color: transparent !important;
}

.mkdf-row-with-bg-pattern{
    position: relative;
}

//inner section in elementor to be centered and wpbakery z index adjustment//
section{
    .mkdf-row-with-bg-pattern{
        margin: 0 auto;
    
        .wpb_column {
            position: relative;
            z-index: 1;
        }

        .mkdf-row-background-pattern-holder{
            z-index: 0;
        }
    }
}

.mkdf-row-background-pattern-holder{

    @include mkdfAbsoluteHolderLayout();
    z-index: -1;

    .mkdf-row-background-pattern-left{
        @include mkdfAbsoluteHolderLayout();
        width: 25.5%;
        background-color: transparent;
    }

    .mkdf-row-background-pattern-right{
        @include mkdfAbsoluteHolderLayout();
        width: 74.5%;
        left: auto;
        right: 0;
        background-image: url('../../assets/img/default_pattern.png');
    }
}

.mkdf-page-bg-pattern-holder{
    @include mkdfAbsoluteHolderLayout();
    z-index: -1;

    .mkdf-page-bg-pattern-column{
        @include mkdfAbsoluteHolderLayout();
        width: 20%;
        background-color: transparent;
        border-right: 1px solid $default-border-color;

        &.mkdf-bg-column-2{
            left: 20%;
        }

        &.mkdf-bg-column-3{
            left: 40%;
            background-image: url('../../assets/img/default_pattern.png');
