.mkdf-close-icon-svg {
    color: #fff;
    width: 18px;
    padding: 11px 10px;

    &:hover {
        path:nth-of-type(1) {
            clip-path: inset(0 0 0 0);
            -webkit-clip-path: inset(0 0 0 0);
            transition: .3s .3s;
        }
        
        path:nth-of-type(2) {
            clip-path: inset(0 0 100% 0);
            -webkit-clip-path: inset(0 0 100% 0);
            transition: .3s;
        }
        
        path:nth-of-type(3) {
            clip-path: inset(0 0 0 0);
            -webkit-clip-path: inset(0 0 0 0);
            transition: .3s .6s;
        }
        
        path:nth-of-type(4) {
            clip-path: inset(0 0 100% 0);
            -webkit-clip-path: inset(0 0 100% 0);
            transition: .3s .1s;
        }
    }
        
    path:nth-of-type(1) {
        clip-path: inset(100% 0 0 0);
        -webkit-clip-path: inset(100% 0 0 0);
        transition: .3s;
    }
        
    path:nth-of-type(2) {
        clip-path: inset(0 0 0 0);
        -webkit-clip-path: inset(0 0 0 0);
        transition: .3s;
    }
        
    path:nth-of-type(3) {
        clip-path: inset(100% 0 0 0);
        -webkit-clip-path: inset(100% 0 0 0);
        transition: .5s;
    }
        
    path:nth-of-type(4) {
        clip-path: inset(0 0 0 0);
        -webkit-clip-path: inset(0 0 0 0);
        transition: .5s;
    }
}

.mkdf-appearing-section {
    position: relative;
    overflow: hidden;

    > div {
        opacity: 0;
        transition: 0s .8s !important; //to override eventual elementor transitions attached to column ie if bg image is set
    }

    &:before, &:after {
        content: "";
        @include mkdfAbsoluteHolderLayout();
    }

    &:before {
        background-color: $first-main-color;
        z-index: 1;
        transform: translateX(-101%);
    }

    &:after {
        background-color: $fourth-blue-color;
        z-index: 2;
        transform: translateX(-101%);
    }

    @keyframes mkdf-appearing-section-animation {
        0% {
            transform: translateX(-101%);
        }
        50% {
            transform: translateX(0);
        }
        100% {
            transform: translateX(101%);
        }
    }

    &.mkdf-appearing-section-appear {

        > div {
            opacity: 1;
        }

        &:after {
            animation: mkdf-appearing-section-animation 1.2s .2s cubic-bezier(1, 0, 0.54, 0.99) forwards;
        }

        &:before {
            animation: mkdf-appearing-section-animation 1.5s cubic-bezier(.86,0,.07,1) forwards;
        }
    }
}
//fix for apperaring in elementor editor
.elementor-editor-active {
    .elementor-column,
    .elementor-widget-mkdf_single_image{
        &.mkdf-appearing-section {
            > div {
                opacity: 1;
            }

            &:after {
                animation: mkdf-appearing-section-animation 1.2s .2s cubic-bezier(1, 0, 0.54, 0.99) forwards;
            }

            &:before {
                animation: mkdf-appearing-section-animation 1.5s cubic-bezier(.86, 0, .07, 1) forwards;
            }
        }
    }
}
.mkdf-appearing-rev-section  {
    position: relative;
    overflow: hidden;

    &:after {
        content: "";
        @include mkdfAbsoluteHolderLayout();
        background-color: #00235a;
        z-index: 1;
    }

    &.mkdf-appearing-section-appear {
        
        &:after {
            animation: mkdf-appearing-rev-section-animation 2s cubic-bezier(0.89, 0.26, 0.28, 0.88) forwards;
        }
    }

    @keyframes mkdf-appearing-rev-section-animation {
        0% {
            transform: translateX(0);
        }
        100% {
            transform: translateX(-100%);
        }
    }
}

.mkdf-landing-bottom-rev-text {
    -webkit-text-stroke-width: 1px;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke-color: #1e3c6f;
    text-transform: uppercase;
    line-height: 180px !important;

    &:after {
        content: 'purchase';
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        -webkit-text-stroke-width: 2px;
        -webkit-text-fill-color: #fff;
        clip-path: inset(0 100% 0 0);
        -webkit-clip-path: inset(0 100% 0 0);
        transition: 2s cubic-bezier(.86,0,.07,1);
    }

    &.mkdf-appearing-section-appear {

        &:after {
            clip-path: inset(0 0 0 0);
            -webkit-clip-path: inset(0 0 0 0);
        }
    }
}