aside.mkdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    @include ipad-landscape{
        margin: 0 0 60px;
    }

    .widget {
        color: $default-heading-color;


        &:last-child {
            margin: 0 !important;
        }
        &.mkdf-author-info-widget{
            margin: 0 0 36px;
        }
        &.widget_categories{
            margin: 0 0 62px!important;
        }
        &.mkdf-blog-list-widget{
            margin: 0 0 72px;

            .mkdf-widget-title{
                margin-bottom: 23px!important;
            }
            .mkdf-post-title{
                color: $default-heading-color;

                a{
                    color: $default-heading-color;
                }
            }
        }
        &.widget_price_filter{
            margin: 0 0 24px;
        }


    }
}

.wpb_widgetised_column {
    position: relative;

    .widget{
        .mkdf-aiw-title{
            color: $default-heading-color;
        }

        .mkdf-widget-title-holder{
            .mkdf-widget-title{
                color: $default-heading-color!important;
            }
        }
    }

}

aside.mkdf-sidebar,
.wpb_widgetised_column,
.elementor-widget.elementor-widget-sidebar
{
    // widgets spacing
    $widget-bottom-margin: 34px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 15px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: inherit;

    // links
    $widget-link-color: $first-main-color;
    $widget-link-hover-color: darken($default-text-color, 35%);

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;

    @import 'widgets/default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';

    .widget{
        &.widget_search{
            margin: 0 0 50px;
        }

        &.widget_categories{
            margin: 0 0 42px;
            text-transform: uppercase;
        }

        &.widget_media_image {

            .mkdf-widget-title-holder {
                margin-bottom: 23px;
            }
        }
        &.widget_media_image{
            margin: 0 0 42px;
        }
    }

    .mkdf-blog-list-holder{
        margin-top: 3px;

        &.mkdf-normal-space{
            .mkdf-outer-space .mkdf-item-space{
                margin: 0 0 21px;
            }

            margin: 0 0 -21px;
        }
    }
}